body {
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    background: linear-gradient(0.05turn, #b6b327, #508b22, #f69d3c);
    background-repeat: no-repeat;
    height: 100vh;
}
form {
    margin-top: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
}
input {
    padding: 10px;
    margin: 5px;
    font-size: 18px;
}
h1{
    display: flex;
    align-items: center;
    justify-content: center;
}
p {
    display: flex;
    align-items: center;
    justify-content: center;
}
.photo canvas {
    box-shadow: 1px 2px 16px 0px rgba(0,0,0,0.75);
}
.text_container{
    padding: 10px;
    background-color: #f5fff579;
    border-radius: 8px;
}
.result_container{
    width: 100%;
    justify-content: center;
    display: flex;
    align-items: center;

}

@media only screen and (max-width: 500px) {
    .photo canvas {
        width: 100%;
        height: auto;
      }
      input {
        padding: 0;
      }
      @media only screen and (max-width: 430px) {
        .input_container {
            width: 120px;
      }
      @media only screen and (max-width: 340px) {
        .input_container {
            width: 80px;
        }
        @media only screen and (max-width: 310px) {
            .input_container {
                width: 50px;
          }
      }
    }
}
     
}